import { firestore } from "@/firebase";
import {
    getFirestore,
    doc,
    collection,
    where,
    query,
    getDocs,
    deleteDoc,
} from "firebase/firestore";

export default {
    state: {
        ticketList: null,
    },
    mutations: {
        fetchTicketList(state, ticketList) {
        state.ticketList = ticketList;
        },
    },
    actions: {
        async fetchTicketList({ commit, rootState }) {
            const userUid = rootState.auth.user.uid;        
            let ticketArray = [];
            const queryRef = query(
                collection(getFirestore(), "tickets"),
                where("uid", "==", userUid)
            );
            await firestore
                .getDocs(queryRef)
                .then((response) => {
                response.forEach((element) => {
                    const elementData = element.data();
                    const index = ticketArray.indexOf(elementData.id);
                    if (index === -1) {
                        ticketArray.push(elementData);
                    }
                });
                commit("fetchTicketList", ticketArray);
                })
                .catch(() => {
                throw Error("Error due fetch Tickets.");
            });       
        },

        async addNewTicket({ rootState, dispatch }, newTicket) {
            const userUid = rootState.auth.user.uid;
            const collectionRef = collection(getFirestore(), "tickets");
            newTicket.uid = userUid;
            await firestore
                .addDoc(collectionRef, newTicket)
                .then(() => dispatch("fetchTicketList"))
                .catch(() => {
                throw Error("Error due add new Ticket.");
                });            
        },

        async checkTicket({ dispatch }, { id, currentCheckState }) {
            const documentRef = doc(getFirestore(), "tickets", id);
            await firestore
                .updateDoc(documentRef, {
                isChecked: !currentCheckState,
                })
                .then(() => dispatch("fetchTicketList"))
                .catch(() => {
                throw Error("Error due check Ticket.");
            });
        },

        async deleteTicket({ dispatch, rootState }, dayid) {
            const userUid = rootState.auth.user.uid;                    
            const queryRef = query(
                collection(getFirestore(), "tickets"),
                where("uid", "==", userUid),
                where("id", "==", dayid),
            );
            const querySn = await getDocs(queryRef);
            if ( querySn.docs.length !==0 ){
                await deleteDoc(querySn.docs[0].ref)
                        .then(() => dispatch("fetchTicketList"))
                        .catch(() => {
                            throw Error("Error due delete Ticket.");  
                        });
            }            
        },
    },
};